<template>
  <v-menu
    bottom
    left
    min-width="200"
    offset-y
    origin="top right"
    transition="scale-transition"
  >
    <template v-slot:activator="{ attrs, on }">
      <v-btn class="ml-2" min-width="0" text v-bind="attrs" v-on="on">
        <v-icon>mdi-account</v-icon><b class="">{{ userDetails.full_name }}</b>
      </v-btn>
    </template>

    <v-list :tile="false" flat nav>
      <template>
        <app-bar-item to="/">
          <v-list-item-title><v-btn @click="logout();">Logout</v-btn></v-list-item-title>
        </app-bar-item>
      </template>
    </v-list>
  </v-menu>
</template>

<script>
import { mapGetters } from "vuex";
import axios from "axios";
export default {
  name: "DefaultAccount",
  methods: {
    logout(){
      const params = { crossDomain: true, withCredentials: true };
      const api = axios.create({ baseURL: "" });
      api
        .get("http://digitad.us-east-2.elasticbeanstalk.com/server/php/logout.php", params)
        .then((result) => {
          let feedback = result.data;
          //console.log(user_info);
          if (feedback !== 0) {
             window.location = "http://digitad.us-east-2.elasticbeanstalk.com/login";
          } else {
            window.location = "http://digitad.us-east-2.elasticbeanstalk.com/login";
          }
        })
        .catch((err) => {
          window.location = "http://digitad.us-east-2.elasticbeanstalk.com/login";
          //console.log(err);
        });
    },
    authenticateClient() {
      const params = { crossDomain: true, withCredentials: true };
      const api = axios.create({ baseURL: "http://digitad.us-east-2.elasticbeanstalk.com/" });
      api
        .get("http://digitad.us-east-2.elasticbeanstalk.com/server/user_info.php", params)
        .then((result) => {
          let user_info = result.data;
          //console.log(user_info);
          if (user_info !== 0) {
            this.userDetails.user_id = user_info.id;
            this.userDetails.f_name = user_info.f_name;
            this.userDetails.l_name = user_info.l_name;
            this.userDetails.email = user_info.email;
            this.userDetails.country = user_info.country;
            this.userDetails.companyName=user_info.companyName;
            this.userDetails.city=user_info.city;
            this.userDetails.homeAddress=user_info.user_address;
            this.userDetails.position_in_company=user_info.position_in_company;
             this.userDetails.companyAddress=user_info.company_address;
             this.userDetails.company_reg_num=user_info.company_reg_num;
            this.userDetails.full_name =
              user_info.f_name + " " + user_info.l_name;
          } else {
            window.location = "http://digitad.us-east-2.elasticbeanstalk.com/login";
          }
        })
        .catch((err) => {
         window.location = "http://digitad.us-east-2.elasticbeanstalk.com/login";
          console.log(err);
        });
    },
    fetch_user_campaigns() {
      const params = { crossDomain: true, withCredentials: true };
      const api = axios.create({ baseURL: "http://digitad.us-east-2.elasticbeanstalk.com" });
      api
        .get(
          "http://digitad.us-east-2.elasticbeanstalk.com/server/fetch_campaigns.php",
          params
        )
        .then((result) => {
          let campaigns = result.data;
          //console.log(campaigns);
          //console.log(campaigns);
          if(campaigns.length>0){
              campaigns.forEach((campaign) => {
                console.log(campaign)
              if (campaign.approved == 0) {
                this.unApprovedCampaigns.push(campaign);
              } else if (campaign.approved == 1 && campaign.paid == 0) {
                this.not_paid_campaigns.push(campaign);
                this.library_campaigns.push(campaign);
              } else if (campaign.approved == 1 && campaign.paid == 1) {
                this.activeCampaigns.push(campaign);
                this.library_campaigns.push(campaign);
              }
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    fetchArchives() {
      const params = { crossDomain: true, withCredentials: true };
      const api = axios.create({ baseURL: "http://digitad.us-east-2.elasticbeanstalk.com" });
      api
        .get("http://digitad.us-east-2.elasticbeanstalk.com/server/fetch_archives.php", params)
        .then((result) => {
          let campaigns = result.data;
          //console.log(campaigns);
         if(campaigns.length>0){
            campaigns.forEach((campaign) => {
              //push to archive store
              this.archivedCampaigns.push(campaign);
              this.library_campaigns.push(campaign);
            });
         }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },

  data: () => ({
    profile: [
      { title: "Profile" },
      { title: "Settings" },
      { divider: true },
      { title: "Log out" },
    ],
    credentials: {
      name: "",
      email: "",
    },
  }),
  mounted() {
    //authenticate client
    this.authenticateClient();
    //fetch all user campaigns
    this.fetch_user_campaigns();
    //fetch archives
    this.fetchArchives();
  },
  computed: mapGetters([
    "activeCampaigns",
    "archivedCampaigns",
    "unApprovedCampaigns",
    "not_paid_campaigns",
    "userDetails",
    "library_campaigns"
  ]),
};
</script>
